import axios from 'axios';
import * as Config from '../constants/Config';
const COMPANY_ID = "60ec16851bea41146e5ba57e"
// API to get products from mock server
export const  getProducts = async function () {
    var response = await callApi(`products?offset=0`, 'GET', null, null);
    let results = response.data.results.map((item,index) => {
        return {
            "vid":index,
            "name" : item.nameProduct,
            "category" : [
                item.categories.nameCategory
            ],
            "brands" : [
                item.producerId
            ],
            "shortDesc" : item.description,
            "id" : item.id,
            "price" : item.price,
            "smPictures" : item.gallery,
            "pictures" : [
              item.image
            ],
            "ratings" : item.ratings,
            "reviews" : item.reviews,
            "stock": item.numberAvailable,
                                                
            "new": item.new,
                                                          
            "top": item.top,
                         
            "size": item.size,
           
           
            "salePrice": item.salePrice,
            "discount": item.discount,
            "featured":item.featured,
            "until": item.until,
            "variants": item.variants,
          };
    });
    return results
}
export const  createOrder = async function (body) {
  var response = await callApi(`orders`, 'POST', body, null);
  return response
}
async function callApi(endpoint, method = 'GET', body, token) {
    try {
      let data;
      if (token !== undefined && token !== null && token !== '') {
        console.log("token " +token)
        data =  axios({
          method: method,
          url: `${Config.API_URL}/${endpoint}`,
          headers: {
            Accept: 'application/json',
            Authorization: 'Bearer '+ token,
            Companyid:COMPANY_ID
        },
          data: body
        });
        return data;
      } else {
        console.log("no token " +Config.API_URL_PUBLIC+"/"+endpoint)
        data =  axios({
          method: method,
          url: `${Config.API_URL_PUBLIC}/${endpoint}`,
          headers: {
            Companyid:COMPANY_ID
        },
          data: body
        });
        return data;
      }
    }
    catch (err) {
      
    }
}

